import { Component, Input, OnInit } from '@angular/core';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IPsComplexPayIdTypesDetailsOptionsExposed } from './ps-complex-payId-types-details.component.interfaces';

@Component({
  selector: 'ps-complex-payId-types-details',
  templateUrl: './ps-complex-payId-types-details.component.html',
  styleUrls: ['./ps-complex-payId-types-details.component.scss'],
})
export class PsComplexPayIdTypesDetailsComponent extends PsBaseFieldComponent implements OnInit {
  @Input() options: IPsComplexPayIdTypesDetailsOptionsExposed;
  selectedType = '';

  constructor(public common: PsCommonService, public logger: LoggerService) {
    super(common, logger);
  }

  ngOnInit() {
    super.init();
  }

  onPayIdTypeSelected(event?) {
    if (event && event.itemValue) {
      this.selectedType = event.itemValue;
    } else {
      this.selectedType = null;
    }
    this.onPsChange.emit(event);
  }

}
