import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsPayIdBeneficiaryComponent } from 'src/app/commonBussinessSRC/psComponents/ps-beneficiary/ps-payId-beneficiary/ps-payId-beneficiary.component';
import { IOptionsPsComplexAmountExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-amount/ps-complex-amount.component.interfaces';
import { PsComplexPayIdTypesDetailsComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-payId-types-details/ps-complex-payId-types-details.component';
import { IPsComplexPayIdTypesDetailsOptionsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-payId-types-details/ps-complex-payId-types-details.component.interfaces';
import { IOptionsPsComplexSelectBranchComponentExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-select-branch/ps-complex-select-branch.component.interface';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { IPsLookupOwnPayIdsComponentOptionsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-payIds/ps-lookup-own-payIds.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsComplexSegment, IOptionsPsContainerPanel, IOptionsPsSelectToggle, IOptionsTemplateStepper, IPageCommon } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'payId-transfer',
  templateUrl: './payId-transfer.page.html',
  styleUrls: ['./payId-transfer.page.scss'],
})
export class PayIdTransferPage extends OmniBasePage implements OnInit {
  public formGroup = new FormGroup({});
  @ViewChild(PsComplexPayIdTypesDetailsComponent) complexPayIdTypesDetailsComp: PsComplexPayIdTypesDetailsComponent;
  transferVO = {};

  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'payId_transfer_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['payId_transfer_details'],
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
      group: this.formGroup,
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.handleScreenVo(response));
          });
        },
        executionClass: this,
        params: [this]
      }
    },
    requestObject: this.transferVO
  }

  payidTransferPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'payid_transfer_key',
    iconName: 'document',
    expanded: true
  };

  branchPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'select_branch_key',
    iconName: 'document',
    expanded: true
  };

  fromPayIdLookupOptions: IPsLookupOwnPayIdsComponentOptionsExposed = {
    labelKey: 'from_payid_key',
    placeHolder: 'select_from_payid_key',
    component: PsPayIdBeneficiaryComponent,
    group: this.formGroup,
    fcName: 'fromPayId',
    fromTo: 'from',
    requestObject: this.transferVO
  }

  toPayIdLookupOptions: IPsLookupOwnPayIdsComponentOptionsExposed = {
    labelKey: 'to_payid_key',
    placeHolder: 'select_to_payid_key',
    component: PsPayIdBeneficiaryComponent,
    group: this.formGroup,
    fcName: 'toBeneficiary',
    fromTo: 'to',
    requestObject: this.transferVO
  }

  complexSegmentOptions: IOptionsPsComplexSegment = {
    group: this.formGroup,
    segmentOptions: {
      fcName: 'beneficiarySegment',
      segmentList: [{
        selected: true,
        itemValue: 'to_benef',
        description: this.commonProv.translate('to_beneficiary_key'),
      }, {
        selected: true,
        itemValue: 'to_others',
        description: this.commonProv.translate('to_other_key')
      }],
      group: this.formGroup,
    },
    segmentContainerOptions: {
      numberOfSegments: 2,
      namesofSegments: ['beneficiary_segment', 'other_segment'],
    }
  };

  payIdTypesDetailsOptions: IPsComplexPayIdTypesDetailsOptionsExposed = {
    payIdTypesOptions: {
      fcName: 'payIdType',
      group: this.formGroup,
    },
    mobileOptions: {
      fcName: 'mobileNo',
      labelKey: 'mobile_no_key',
      placeHolder: 'enter_mobile_no_key',
      group: this.formGroup
    },
    emailOptions: {
      fcName: 'email',
      labelKey: 'email_address_key',
      placeHolder: 'enter_email_address_key',
      group: this.formGroup
    },
    phoneOptions: {
      fcName: 'phoneNo',
      labelKey: 'phone_number_key',
      placeHolder: 'enter_phone_number_key',
      group: this.formGroup
    }
  }

  complexAmountOptions: IOptionsPsComplexAmountExposed = {
    currenciesOptions: {
      labelKey: 'currency_key',
      placeHolder: 'select_currency_key',
      group: this.formGroup,
      fcName: 'currency'
    },
    amountOptions: {
      labelKey: 'amount_key',
      placeHolder: 'enter_amount_key',
      fcName: 'transferAmount',
      group: this.formGroup,
      type: 'amount',
      decimalPoints: 3
    },
    requestObject: this.transferVO
  };

  ownPurposeOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'own_purpose_key',
    placeHolder: 'enter_the_purpose_to_be_shown_on_your_statement_key',
    group: this.formGroup,
    fcName: 'ownPurpose'
  };

  benefPurposeOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'beneficiary_purpose_key',
    placeHolder: 'enter_the_purpose_to_be_shared_with_beneficiary_bank_key',
    group: this.formGroup,
    fcName: 'benefPurpose'
  };

  saveToBenefToggleOptions: IOptionsPsSelectToggle = {
    labelKey: 'save_beneficiary_key',
    placeHolder: 'save_beneficiar_key',
    group: this.formGroup,
    fcName: 'saveBenef',
    psClass: 'toggleColor'
  };

  selectBranchOptions: IOptionsPsComplexSelectBranchComponentExposed = {
    fcName: 'mapAtmBranchesDropdown',
    group: this.formGroup
  }

  constructor(public navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.commonProv.copyObject(this.transferVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {});
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
  }


  onClickSegment(event) {
    if (event === this.complexSegmentOptions.segmentOptions.segmentList[0].itemValue) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.payIdTypesDetailsOptions.payIdTypesOptions.fcName], 0)
      this.commonProv.setValInsideNestedObj(this.payIdTypesDetailsOptions.payIdTypesOptions.fcName, null, this.transferVO)
      this.complexPayIdTypesDetailsComp.onPayIdTypeSelected();
    } else {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.toPayIdLookupOptions.fcName, this.toPayIdLookupOptions.fcName + '_lookupKey'], 0)
      this.commonProv.setValInsideNestedObj(this.toPayIdLookupOptions.fcName, null, this.transferVO)
      this.formGroup.controls[this.toPayIdLookupOptions.fcName]?.reset();
      this.formGroup.controls[this.toPayIdLookupOptions.fcName + '_lookupKey']?.reset();
    }
    this.formGroup.controls[this.complexSegmentOptions.segmentOptions.fcName].setValue(event);
  }

  savePayIdBeneficiary() {
    let page: IPageCommon;
    page = {
      operID: CommonBussinessConstant.PAYID_BENEFICIARY_OPER_ID,
      title: 'payid_beneficiary_key',
      component: 'add-payId',
      param: {
        payIdType: this.formGroup.controls[this.payIdTypesDetailsOptions.payIdTypesOptions.fcName]?.value,
        mobileNumber: this.formGroup.controls[this.payIdTypesDetailsOptions.mobileOptions.fcName]?.value,
        emailAddress: this.formGroup.controls[this.payIdTypesDetailsOptions.emailOptions.fcName]?.value,
        phoneNumber: this.formGroup.controls[this.payIdTypesDetailsOptions.phoneOptions.fcName]?.value,
        purpose: this.formGroup.controls[this.benefPurposeOptions.fcName]?.value
      }
    };
    this.navService.openPage(page);
  }

  handleScreenVo(resp?) {
    if (this.formGroup.controls[this.saveToBenefToggleOptions.fcName]?.value == true) {
      this.savePayIdBeneficiary();
    }
  }
}
